<template lang="pug">
  div#sellerForm
    div.container
      h1.sellerform__form--title  
      p.sellerform__form--subtitle Volla ()  .
        span    .

      form.sellerform__form--form(
        @submit.prevent="sendPost"
        name="sellerform__form"
      )
        p.sellerform__form--title(required)  
        input.sellerform__form--input(
          type="email" name="email" v-model="email"
          placeholder="   ." required
        )
        p.sellerform__form--title(required)  
        input.sellerform__form--input(
          type="text" name="name" v-model="name"
          placeholder="  ." required
        )
        p.sellerform__form--title(required) 
        input.sellerform__form--input(
          type="tel" name="contact" v-model="contact"
          minlength="8" maxlength="12"
          placeholder="'-'   ." required
        )
        p.sellerform__form--title  
        input.sellerform__form--input(
          type="text" name="site" v-model="site"
          placeholder="  ."
        )
        p.sellerform__form--title SNS 
        input.sellerform__form--input(
          type="text" name="sns" v-model="sns"
          placeholder=" SNS  ."
        )
        p.sellerform__form--title  
        textarea.sellerform__form--input.textarea(
          type="text" name="details" v-model="details"
          placeholder="  ."
        )

        button.sellerform__form--submit(
          name="sellerform__form"
        ) 
</template>

<script>
import axios from 'axios'
import {globalVar} from '@/globalVar'

export default {
  name: 'sellerForm',

  metaInfo: {
    title: globalVar.serviceEn + ' - ' + globalVar.sellerForm,
    titleTemplate: '%s',
  },

  data () {
    return {
      email: '',
      name: '',
      contact: '',
      site: '',
      sns: '',
      details: '',
    }
  },

  methods: {
    sendPost() {
      let baseURI = globalVar.requestSellerUrl

      axios.post(baseURI,
        {
          email: this.email,
          name: this.name,
          contact: this.contact,
          site: this.site,
          sns: this.sns,
          details: this.details,
        },
        {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
          }
        },
      )
      .then(res => {
        console.log(res.data)
      })

      alert(this.name + '    .\n     .')
      // window.close()
      // window.postMessage('close')
    },

  },

}
</script>

<style lang="scss">
$width: 320px;
// $pablet-width: 320px;
$mobile-width: 288px;

#sellerForm {
  text-align: center;
  padding: $grid12x 0 $grid32x;

  .sellerform__form--subtitle {
    font-weight: 300;

    span {
      &::before {
        content: '\A* ';
        white-space: pre;
        font-weight: 900;
        color: $brand-pink;
      }
    }
  }

  .sellerform__form--form {
    width: $width;
    display: block;
    margin: 0 auto;
    text-align: center;
    margin-top: $grid20x;

    // @media #{$pablet} {
    //   width: $pablet-width;
    // }

    @media #{$mobile} {
      width: $mobile-width;
    }

    .sellerform__form--title {
      display: block;
      text-align: left;
      font-weight: 900;
      margin-bottom: $grid;
      @include font-size($grid5x);

      &[required] {
        &::after {
          content: ' *';
          color: $brand-pink;
        }
      }
    }

    .sellerform__form--input {
      border: none;
      width: $width;
      outline: none;
      display: inline-block;
      margin-bottom: $grid12x;
      padding: $grid4x 0 $grid;
      border-bottom: 1px solid $textccc;
      @include transition(all 0.25s ease);

      // @media #{$pablet} {
      //   width: $pablet-width;
      // }

      @media #{$mobile} {
        width: $mobile-width;
      }

      &.textarea {
        resize: none;
        height: $grid32x;
        @include line-height($grid4x);
      }

      &:focus {
        border-bottom: 1px solid $brand-pink;
      }

      &::selection {
        color: #fff !important;
        background-color: $brand-pink !important;
      }

      &::placeholder,
      &::-moz-placeholder,
      &:-ms-input-placeholder,
      &::-ms-input-placeholder,
      &::-webkit-input-placeholder {
        color: $black24;
      }
    }

    .sellerform__form--submit {
      outline: none;
      margin: 0 auto;
      display: block;
      width: $grid48x;
      cursor: pointer;
      height: $grid14x;
      font-weight: 900;
      color: $brand-pink;
      margin-top: $grid8x;
      background-color: #fff;
      border: 2px solid $brand-pink;
      @include transition(all 0.25s ease);
      @include border-radius($grid16x);

      &:hover {
        color: #fff;
        font-weight: 900;
        background-color: $brand-pink;
      }
    }
  }
}
</style>



